<template>
 <div class="myContent">
 	<div class="indent">
 		<div class="indent_title">
 			<div></div>
 			<div><span></span><img src="../../assets/img/my/right.png" alt=""></div>
 		</div>
 		<ul>
 			<li v-for="v in indent">
 				<img :src="v.icon">
 				<p>{{v.title}}</p>
 			</li>
 		</ul>
 	</div>
 	<ul class="list">
 		<li v-for="(v,k) in list">
 			<img :src="v.icon" alt="">
 			<span>{{v.title}}</span>
 			<span class="add">{{v.add}}</span>
 			<img src="../../assets/img/my/right.png" alt="">
 		</li>
 	</ul>
 </div>
</template>

<script>
export default {
  data () {
    return {
    	indent:[{icon:require("../../assets/img/my/order1.png"),title:""},
    		{icon:require("../../assets/img/my/order2.png"),title:""},
    		{icon:require("../../assets/img/my/order3.png"),title:""},
    		{icon:require("../../assets/img/my/order4.png"),title:""}
    	],
    	list:[{icon:require("../../assets/img/my/list1.png"),title:"/"},
    	{icon:require("../../assets/img/my/list2.png"),title:""},
    	{icon:require("../../assets/img/my/list3.png"),title:"",add:"0.00"},
    	{icon:require("../../assets/img/my/list4.png"),title:"",add:"0"},
    	{icon:require("../../assets/img/my/list5.png"),title:"",add:""},
    	{icon:require("../../assets/img/my/list6.png"),title:"",add:"5%"},
    	{icon:require("../../assets/img/my/list7.png"),title:"",add:"50"},
    	{icon:require("../../assets/img/my/list8.png"),title:""},
    	{icon:require("../../assets/img/my/list9.png"),title:""},
    	{icon:require("../../assets/img/my/list10.png"),title:""}
    	]
    }
  }
}
</script>


<style scoped lang='stylus'>
@import '../../assets/common/stylus/common.stylus'
.myContent
	color:gray;
	.indent
		padding:pxtorem(24px) 0;
		border-top:1px solid #ccc;
		border-bottom:1px solid #ccc;
		margin-top:pxtorem(60px)
		.indent_title
			position:relative;
			width:pxtorem(750px);
			height:pxtorem(60px);
			font-size:pxtorem(26px);
			div
				position:absolute;
			div:first-child
				left:pxtorem(20px);
				top:pxtorem(0);
			div:last-child
				right:pxtorem(20px);
				top:pxtorem(0);
				span,img
					vertical-align:middle;
					margin-right:pxtorem(10px)
				img
					width:pxtorem(20px);
					height:pxtorem(34px);		
		ul
			display:flex;
			width:pxtorem(750px);
			li
				flex:1;
				text-align:center;
	
.list
	background:#f2f2f2;
	li
		width:pxtorem(750px);
		height:pxtorem(100px);
		position:relative;
		line-height:pxtorem(100px);
		border-bottom:1px solid #ccc;
		background:white;
		img
			vertical-align:middle;
		img:first-child
			vertical-align:middle;
			width:pxtorem(36px);
			height:pxtorem(36px);
			padding-left:pxtorem(30px)
		img:last-child
			width:pxtorem(20px);
			height:pxtorem(34px);
			position:absolute;
			right:pxtorem(20px);
			top:pxtorem(33px)
		span
			vertical-align:middle;
			padding-left:pxtorem(20px)
		.add
			position:absolute;
			right:pxtorem(80px)
	li:nth-child(2)
		margin-bottom:pxtorem(20px)
	li:nth-child(5)
		margin-bottom:pxtorem(20px)
	li:nth-child(7)
		margin-bottom:pxtorem(20px)	
	li:last-child
		padding-bottom:pxtorem(102px)
</style>



