<template>
<div @touchstart="back"></div>
</template>

<script>
export default {
  data () {
    return {
    }
  },
  methods:{
  	back:function(e){
  		this.$router.go(-1);
  		e.stopPropagation();
  	}
  }
}
</script>


<style scoped lang='stylus'>
@import '../../assets/common/stylus/common.stylus'
div
	width:pxtorem(40px);
	height:pxtorem(40px);
	background:black url('../../assets/img/my/left.png') center center;
	opacity:0.5;
	border:3px solid black;
	border-radius:50%;
	position:fixed;
	top:pxtorem(20px);
	left:pxtorem(30px);z-index:1000; 
</style>



