<template>
 <div class="commContentO">
 	<div class="collect">
 		<div>
	 		<img src="../../assets/img/community/camera.png" alt="">
	 		<span></span>
 		</div>
 		<ul @touchmove="stop">
 			<li v-for="v in collect"><img :src="v" alt=""></li>
 		</ul>
 	</div>
 	 <div class="show">
		<div v-for="(v,k) in recommendArr">
			<div class="username">
				<img :src="v.authorUrl" alt="">
				<span> {{v.name}}</span>
			</div>
			<mt-swipe :auto="0" :show-indicators="false" :stopPropagation="true" :continuous="false" class="pic">
			  <mt-swipe-item v-for="(val,key) in v.banner">
			  	<router-link to="./comment" @click.native="fn(k)"><img :src="val" alt="" ></router-link>
			  	<div class="picTip">{{key+1}}/{{v.banner.length}}</div>
			  </mt-swipe-item>
			</mt-swipe>
			<ul class="clear">
				<li><img src="../../assets/img/community/collect.png" alt=""><span>{{show[k]['head'].length}}</span></li>
				<li><img src="../../assets/img/community/good.png" alt=""><span></span></li>
				<li><img src="../../assets/img/community/talk.png" alt="">{{show[k]['comment'].length}}</li>
			</ul>
			<p style="background:white;">{{v.txt}}</p>
			
		</div>
	</div>
 </div>

</template>

<script>
import axios from 'axios';
import { mapGetters, mapActions } from 'vuex';
export default {
  data () {
    return {
    	collect:[],
    	recommendArr:[]
    }
  },
  computed:{
  	...mapGetters({
	  	show:"getShow",
	  	comment:"getBigcomment"
 	 })
  },
  mounted:function(){
  	var that=this;
  	axios.get("/community").then(function(res){
  		that.collect=res.data.collect;
  	});
  	axios.get("/community/recommend",{
  		params: {
          name: "recommend"
        }
  	}).then(function(res){
  		that.recommendArr=res.data.show
  	})
  	console.log(this.comment.length)

  },
  methods:{
  	stop:function(e){
  		e.stopPropagation();
  	},
  	 
  	 ...mapActions([
      "setBigcomment"]),
  	 fn:function(key){
  	 console.log(key)
     this.setBigcomment(this.recommendArr[key])
  	

  	}
  }
  
}
</script>


<style scoped lang='stylus'>
@import '../../assets/common/stylus/common.stylus'
::-webkit-scrollbar
	width:0;
	height:0;
	display:none;
.pic
	width:pxtorem(750px);
	height:pxtorem(580px);
	img
		width:pxtorem(750px);
		height:pxtorem(580px);
.collect
	padding-bottom:pxtorem(14px);
	border-bottom:1px solid #ccc;
	>div
		height:pxtorem(60px);	
		line-height:pxtorem(60px);
		>img
			width:pxtorem(45px);
			height:pxtorem(45px);
			vertical-align:middle;
		span
			vertical-align:middle;
			color:gray;
	ul
		white-space:nowrap;
		overflow-x:scroll;
		padding-top:pxtorem(10px);
		li
			display:inline-block;
			width:pxtorem(320px);
			height:pxtorem(160px);
			padding-left:pxtorem(14px);
			img
				width:100%;
				height:100%;
				border-radius:pxtorem(10px);
.show
	background:#f2f2f2;
	>div
		margin-bottom:pxtorem(16px);
		position:relative;
		background:white;
	ul,p
		padding-top:pxtorem(20px);
	li
		float:left;
		img
			width:pxtorem(40px);
			height:pxtorem(40px);
			vertical-align:middle;
		span
			vertical-align:middle;
	>div:last-child
		padding-bottom:pxtorem(100px);
.username
	padding:pxtorem(20px) 0 pxtorem(20px)  pxtorem(20px);
	background:white;
	img
		width:pxtorem(55px);
		height:pxtorem(55px);
		border-radius:50%;
		vertical-align:middle;
	span
		vertical-align:middle;
.picTip
	width:pxtorem(65px);
	height:pxtorem(65px);
	background:#ccc;
	border-radius:50%;
	position:absolute;
	right:0;
	bottom:0;
	text-align:center;
	line-height:pxtorem(65px);
</style>



