<template>
 <div class="homeContentT">
 	<img :src="banner" alt="">
 	<ul class="goods">
 		<li v-for="v in goods">
 			<img :src="v.url" alt="">
 			<p>{{v.title}}</p>
 			<p>{{v.price}}</p>
 		</li>
 	</ul>
 </div>
</template>

<script>
import axios from 'axios';
export default {
  data () {
    return {
    	banner:require("../../assets/img/home/otherBanner.png"),
    	goods:[]

    }
  },
  mounted:function(){
  	var that=this;
  	 axios.get("/home").then(function(res){
        that.goods=res.data.Decoration
      });
  }
}
</script>


<style scoped lang='stylus'>
@import '../../assets/common/stylus/common.stylus'
.goods
	li
		width:pxtorem(350px);
		height:pxtorem(446px);
		display:inline-block;
		margin-left:pxtorem(15px);
		text-align:center;
		img
			width:100%;
	
</style>



