<template>
  <div class="">
   <mt-swipe :auto="0" :show-indicators="false" :stopPropagation="true" :continuous="false" class="aaa">
        <mt-swipe-item v-for="(item,key) in comment.banner" class="headImge">
            <img v-bind:src="item" class="img">
            <span>{{[((key+1)/comment.banner.length)*5]+"/5"}}</span>
           </mt-swipe-item> 
      </mt-swipe>
      <ul class="all">
       <li class="comment">
         <img v-bind:src="comment.authorUrl">
         <span>{{comment.name}}</span>
       </li> 
       <li>  
          <p>{{comment.txt}}</p>
          <span>2017.03.23</span>
       </li> 
       <li class="hand">
        <p><img src="../../assets/img/community/good.png" @click="play">
          <img src="../../assets/img/community/good-s.png" v-show="show" @click="play">
        </p>
        <p><img src="../../assets/img/community/collect.png"@click="fight">
          <img src="../../assets/img/community/collect-s.png" v-show="hide" @click="fight">
        </p> 
       </li> 
       <li class="commentimg" >
        <p v-for="item in comment.head">
         <img v-bind:src="item">
        </p> 
         <div class="a"></div>
       </li> 
       <li class="last"> 
         <span>1</span>
         <p>
           <span></span>
           <span></span>
         </p>
       </li>
     </ul>
     <ul v-for="(item,index) in comment.content" class="conmment_cir">
          <li>{{item}}</li>
          <li><span class="restore"></span></li>
          <li v-show="open[index]">
             <input type="text" class="input" >
             <input type="button" class="button" value="" @click="send(index)">
          </li>
     </ul>
     <div class="big_img">
       <ul v-for="item in comment.contentimg" class="comment_img">
          <li><img v-bind:src="item"></li>
       </ul>
     </div>
     <div class="right_time">
        <ul v-for="(item,index) in comment.time">
           <li>
             <span @click="wait(index)"></span>
             <span>{{item}}</span>
            </li>
        </ul>
        <p v-show="Lhide"></p>
     </div>
     
     <!-- <div>{{time | formatDate}}</div> -->
  </div>
</template>

<script>
// leftNrrow
import { mapGetters, mapActions } from 'vuex';
import { Swipe, SwipeItem } from 'mint-ui';
import Vue from 'vue';
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
export default {
   data(){
    return{
        Alldata:[],
        show:false,
        hide:false,
        time:1469281964000,
        open:[false,false,false,false,false],
        Lhide:false
    }
   },
   filters: {
        formatDate(time) {
            var date = new Date(time);
            console.log('')
            return formatDate(date, 'yyyy-MM-dd hh:mm');
        }
    },
   methods:{
   play:function(){
      this.show=!this.show
      var a=document.querySelector(".a")
      if(this.show==true){
          let Myoff = document.createElement("img");
          Myoff.src=this.comment.head[0];
          a.appendChild(Myoff)
          Myoff.style.width = '45%' 
          Myoff.style.borderRadius='50%'
      }else{
        a.innerHTML=""
      }
   },
   fight:function(){
     this.hide=!this.hide
     console.log('aa')
   },
    send:function(index){
        var input=document.querySelectorAll(".input")
        var restore=document.querySelectorAll(".restore");
        var conten=""+input[index].value
         restore[index].innerHTML=conten
    },
    wait:function(index){
        console.log("asd")
        this.open[index]=!this.open[index];
        this.Lhide=!this.Lhide
    }
   },
   computed:{
    ...mapGetters({
      comment:"getBigcomment"
   })
  },
   components:{
   
   },
   mounted:function(){
       this.Alldata=this.comment;
       console.log(this.Alldata.banner)
   }
}
</script>

<style lang="stylus"  scoped>
@import '../../assets/common/stylus/common.stylus'
.aaa
  width:pxtorem(750px);
  height:pxtorem(600px);
  img
    width:pxtorem(750px);
    height:pxtorem(600px);
  span
    color red
    position absolute
    bottom pxtorem(50px)
    right pxtorem(40px) 
    height pxtorem(80px)
    width pxtorem(80px)
    border-radius 50%
    background gray
    opacity 0.5
    text-align center
    line-height pxtorem(80px)
    font-size pxtorem(35px)
 .all
    width pxtorem(750px)
.all>li
    width 90% 
    margin-left 5%   
.comment
    width 90%
    height pxtorem(90px)
    border-bottom pxtorem(1px) solid gray
    position relative      
 .comment>img
    width pxtorem(60px)
    height pxtorem(60px)
    border-radius 50% 
    margin-top pxtorem(10px)
 .comment>span
    position absolute 
    top pxtorem(50px)
    left pxtorem(70px)
    font-size pxtorem(26px)
  .all>li:nth-of-type(2)
    margin-top pxtorem(20px)
    font-size pxtorem(25px)
    line-height pxtorem(40px)
  .all>li:nth-of-type(2)>span
    margin-top pxtorem(25px)
    margin-left 80% 
  .hand>p
    display inline-block
    width pxtorem(100px)
    height pxtorem(100px) 
    border-radius 50%
    border 1px solid black
    margin-top pxtorem(50px)
  .hand>p
    margin-left pxtorem(150px) 
    position relative   
  .hand>p>img 
    width pxtorem(80px)
    height pxtorem(80px)
    margin-top pxtorem(10px)
    margin-left pxtorem(10px)
  .hand>p>img:nth-of-type(2)
    position absolute
    top pxtorem(0px)
    left pxtorem(0px) 
  .all>.commentimg
    width pxtorem(750px)
    height pxtorem(200px)
    background orange
    margin-left pxtorem(0px) 
    margin-top pxtorem(30px)
    position relative
  .commentimg>p
    display inline-block 
    margin-top pxtorem(30px)
    margin-left pxtorem(5px)
   .commentimg>p:nth-of-type(1)
    margin-left pxtorem(20px)   
  .all>.commentimg>p>img
    width pxtorem(70px)
    height pxtorem(70px)
    border-radius 50% 
  .a  
     position absolute
     top  pxtorem(30px)
     left pxtorem(480px)
  .all>.last
     width 100%
     margin-left pxtorem(0px)
     height pxtorem(100px)
     border pxtorem(1px) solid gray
     line-height pxtorem(95px)

   .all>.last>span
     margin-left pxtorem(30px)  
 .all>.last>p
     display inline-block
     width pxtorem(205px)
     height pxtorem(60px)
     margin-left pxtorem(350px)
     margin-top pxtorem(2px)
     text-align center
     line-height pxtorem(60)
 .all>.last>p>span
     width pxtorem(100px)
     height pxtorem(60px)
     border 1px solid red
     display inline-block
     background gray
     color white
 .all>.last>p>span:nth-of-type(2)
     margin-left pxtorem(-8px) 
     margin-top pxtorem(10px)
 .restore
     margin-left pxtorem(100px)
         
 .conmment_cir
     margin-left pxtorem(0px)
     height pxtorem(230px)
     width pxtorem(750px)
     border-bottom pxtorem(1px) solid #eee
     border pxtorem(1px) solid white       
 .conmment_cir li:nth-of-type(1)
     margin-top pxtorem(78px)
     margin-left pxtorem(10px)
 .conmment_cir li:nth-of-type(2)
     width pxtorem(750px) 
     height pxtorem(30px)
     margin-top pxtorem(20px)    
 .conmment_cir li:nth-of-type(3)
     position absolute
     z-index 2
     width pxtorem(750px)
     height pxtorem(70px)
     margin-top pxtorem(10px)
     background orange
 .input
     width pxtorem(480px)
     height pxtorem(50px)
     margin-top pxtorem(10px)
     border 1px solid red
     float left
     margin-left pxtorem(100px)
.button
     width pxtorem(80px)
     height pxtorem(50px) 
     border 1px solid red
     margin-top pxtorem(10px)
     float left
     color black
 .big_img
     position relative
     top pxtorem(-670px)
     left pxtorem(20px) 
     z-index -1 
.big_img>.comment_img
     margin-top pxtorem(180px)
.big_img>.comment_img:nth-of-type(1)
      margin-top pxtorem(0px)  
.big_img>.comment_img>li>img
    width pxtorem(50px)
    height pxtorem(50px)
    border-radius 50%
.right_time
    position absolute
    top pxtorem(1350px)
    left pxtorem(550px) 
    z-index 0  
.right_time>ul>li
    width pxtorem(130px)
    height pxtorem(230px)
    overflow hidden
.right_time>ul>li>span:nth-of-type(2)
    display block
    margin-top pxtorem(60px)     


       

   
</style>
</style>



