<template>
	<div class="community">
 		<tL-header  title="">
      <span slot="right" class="edit" v-if="editBol" @click="editFn"></span> 
      <span slot="right" class="edit" v-else @click="editFn"></span> 
    </tL-header>
	 	<tL-nav></tL-nav>
    <ul class="protect clear">
      <li>
        <img src="../../assets/img/shop/shop/gou.png" alt="">
        <span>30</span>
      </li>
      <li>
        <img src="../../assets/img/shop/shop/gou.png" alt="">
        <span>24</span>
      </li>
      <li>
        <img src="../../assets/img/shop/shop/gou.png" alt="">
        <span>88</span>
      </li>
    </ul>
    <sCContent></sCContent>
	</div>
</template>

<script>
import header from '../common/header';
import nav from '../common/nav';
import sCContent from './sCContent'
import bus from '../../../static/js/bus'
export default {
  data () {
    return {
      editBol:true
    }
  },
  components:{
  	"tL-header":header,
  	"tL-nav":nav,
    "sCContent":sCContent
  },
  methods:{
    editFn:function(){
      var bol = this.editBol;
      bol=!bol;
      this.editBol=bol;
      bus.$emit("edit",this.editBol)
    }
  }
}
</script>


<style scoped lang='stylus'>
@import '../../assets/common/stylus/common.stylus'
.protect
  width:pxtorem(750px);
  height:pxtorem(60px);
  line-height:pxtorem(60px);
  position fixed;
  display:flex;
  top:pxtorem(121px);
  border-bottom:1px solid #ccc;
  background:white;
  z-index:10;
  li
    flex:1;
    text-align:center;
    img,span
      vertical-align:middle;
      color:gray;
      img
        width:pxtorem(36px);
        height:pxtorem(36px);
.edit
  font-size:pxtorem(30px);
  color:red;
</style>



