<template>
	<div class="community">
 		<tL-header search=false>
		  <img src="../../assets/img/my/setting.png" alt="" slot="left">
	 	</tL-header>
	 	<tL-nav></tL-nav>
    <div class="login">
      <img src="../../assets/img/my/name-bg.png"><br>
      <button></button>
    </div>
    <tL-myContent></tL-myContent>
	</div>
</template>

<script>
import header from '../common/header';
import nav from '../common/nav';
import myContent from './myContent'


export default {
  data () {
    return {
    }
  },
  components:{
  	"tL-header":header,
  	"tL-nav":nav,
    "tL-myContent":myContent
  }
}
</script>


<style scoped lang='stylus'>
@import '../../assets/common/stylus/common.stylus'
.login
  text-align:center;
  padding-top:pxtorem(140px);
  img
    width:pxtorem(140px);
    height:pxtorem(140px);
  button
    width:pxtorem(270px);
    height:pxtorem(80px);
    outline:none;
    font-size:pxtorem(30px);
    background-color:#f9cf18;
    border:none;
    border-radius:pxtorem(15px);
    color:gray;
    margin-top:pxtorem(36px);
</style>



