<template>
 	<div class="pay">
 		<back></back>
 		<tl-header title=""></tl-header>
 		<ul class="goodsList">
 			<li> <span> ></span></li>
 			<li v-for="v in pay" class="goods">
 				<img :src="v.url" alt="">
 				<div>
	 				<!--  -->
	 				<p>{{v.kind}}</p>
	 				<p>{{v.price}}</p>
	 				<p>{{v.num}}</p>
 				</div>
 				<!--  -->
 			<!-- 	<div>
 					<span>{{v.buyNum*v.price}}</span>
 				</div> -->
 			</li>
			<li> <span>0 ></span></li>
			<li> <span>{{count}}</span></li>
			<li> <span>88 {{count>=88?0:6}}</span></li>
 		</ul>
 		<div></div>
 		<div class="payWay">
 			<div>  <input type="radio" name="pay" value="ali">	<img src="../../assets/img/pay/alipay.png" alt=""><span></span></div>
 			<div> <input type="radio" name="pay" value="wx">	<img src="../../assets/img/pay/wx.png" alt=""><span></span></div>
 		</div>
 		<div class="footer">
 			<span><span>{{count>=88?count:count+6}}</span></span>
 			<button></button>
 		</div>
 	</div>
</template>

<script>
import back from '../common/back';
import header from '../common/header';
import { mapGetters, mapActions } from 'vuex';
export default {
  data () {
    return {

    }
  }, 
  computed: {
  	...mapGetters({
  	pay : 'getPay',
  	}),
  	count:function(){
  		var sum=0;
  		for(var i=0;i<this.pay.length;i++){
  			sum+=this.pay[i].num*this.pay[i].price
  		}
  		return sum;
  	}

	},
  components:{
  	back:back,
  	"tl-header":header
  },
  methods:{
  },
  mounted:function(){
  	console.log(this.pay)
  }
}
</script>


<style scoped lang='stylus'>

@import '../../assets/common/stylus/common.stylus'
.pay
	background:#f2f2f2;
.goodsList
	color:gray;
	padding-top:pxtorem(122px);
	li
		height:pxtorem(100px);
		line-height:pxtorem(100px);
		background:white;
		margin-bottom:pxtorem(10px);
	li
		position:relative;
		span
			position:absolute;
			right:pxtorem(30px);
		
.goodsList
	.goods
		position:static;
		height:pxtorem(100px);
		line-height:normal;
		img
			margin-left:pxtorem(50px);
			width:pxtorem(100px);
			height:pxtorem(100px);
		div
			margin-left:pxtorem(50px);
			display:inline-block;
			vertical-align:top;
		div:first-of-type
			
			p
				height:pxtorem(30px);
				
.payWay
	background:white;
	padding-top:pxtorem(20px);
	>div
		display:inline-block;
		width:pxtorem(320px);
		text-align:center;
		input[type=radio]
			width:pxtorem(30px);
			height:pxtorem(30px);
			outline:none;
			vertical-align:middle;
		img
			width:pxtorem(80px);
			height:pxtorem(80px);
			vertical-align:middle;
		span
			vertical-align:middle;
			padding-left:pxtorem(20px);

.footer
	position:fixed;
	bottom:0;
	left:0;
	width:pxtorem(750px);
	height:pxtorem(100px);
	line-height:pxtorem(100px);
	text-align:center;
	background:white;
	button
		width:pxtorem(200px);
		height:pxtorem(50px);
		border:none;
		outline:none;
		font-weight:bolder;
		font-size:pxtorem(20px);
		background:yellow;
		margin-left:pxtorem(10px);
	>span
		span
			color:red
</style>



