<template>
 	<ul>
 		<li>
 			<slot name="left"></slot>
 		</li>
 		<li>
 			<input type="text" placeholder=" " v-if="bol">
 			<span>{{title}}</span>
 		</li>
 		<li>
 			<slot name="right"></slot>
 		</li>
 	</ul>
</template>

<script>
export default {
  props:["title"],
  data () {
    return {
    	bol:true
    }
  },
  mounted:function(){
  	if(window.location.hash.indexOf("#/home")!=-1||window.location.hash.indexOf("#/community")!=-1){
  		this.bol=true;
  	}else{
  		this.bol=false;
  	}
  }
}
</script>


<style scoped lang='stylus'>
@import '../../assets/common/stylus/common.stylus'
ul
	position:fixed;	
	width:pxtorem(750px);
	height:pxtorem(120px);
	border-bottom:1px solid #ccc;
	background-color:white;
	z-index:100;
	li
		position:absolute;
		height:pxtorem(120px);
		line-height:pxtorem(120px);
	li:first-child
		left:pxtorem(10px);
		top:0;
		font-weight:900;
		span
			font-weight:bolder;
			font-size:pxtorem(35px);
			color:#ccc;
		img
			width:pxtorem(64px);
			height:pxtorem(64px);
			vertical-align:middle;
	li:nth-child(2)
		left:50%;
		width:pxtorem(500px);
		margin-left:pxtorem(-250px);
		text-align:center;
		input
			width:pxtorem(450px);
			height:pxtorem(50px);
			border:none;
			outline:none;
			background-color:#f2f2f2;
			border-radius:pxtorem(10px);
		span
			font-size:pxtorem(35px);
			font-weight:bolder;
			color:#ccc;
	li:last-child
		right:pxtorem(10px);
		top:0;
		img
			width:pxtorem(64px);
			height:pxtorem(64px);
			vertical-align:middle;
		
</style>



