<template>
	<div class="homeContent">
		<div class="homeContent_nav">
			<ul>
				<li v-for="(v,k) in nav" @touchstart="moveTab(k)">{{v}}</li>
			</ul>
			<span class="moveTab" ref="moveTab"></span>
		</div>
		<mt-tab-container v-model="active" :swipeable="true">

		  <mt-tab-container-item id="0">
				<commContentO></commContentO>
		  

		  </mt-tab-container-item>

		  <mt-tab-container-item id="1">

		    <commContentT></commContentT>

		  </mt-tab-container-item>

		  <mt-tab-container-item id="2">

		   	<commContentO></commContentO>

		  </mt-tab-container-item>

		   <mt-tab-container-item id="3">

		   	<commContentO></commContentO>

		  </mt-tab-container-item>

		   <mt-tab-container-item id="4">

		   	<commContentO></commContentO>

		  </mt-tab-container-item>

		   <mt-tab-container-item id="5">

		   	<commContentO></commContentO>

		  </mt-tab-container-item>

		</mt-tab-container>
	</div>
</template>

<script>
import commContentO from './commContentO';
import commContentT from './commContentT';
export default {
  data () {
    return {
    	nav:["","","","","",""],
    	active:"0"
    }
  },
  watch:{
  	active:function(){
  		this.$refs.moveTab.style.left=Number(this.active)*1.66667+"rem";
  	}
  },
  methods:{
  	moveTab:function(index){
  		this.$refs.moveTab.style.left=index*1.66667+"rem";
  		// this.$refs.moveContent.style.left=-index*10+"rem";
  		this.active=index.toString();
  	}
  },
  components:{
  	commContentO:commContentO,
  	commContentT:commContentT
  },
  mounted:function(){

  }
}
</script>


<style scoped lang='stylus'>
@import '../../assets/common/stylus/common.stylus'
.homeContent_nav
	position:relative;
	border-bottom:1px solid #ccc;
	padding-top:pxtorem(120px);
ul
	white-space:nowrap
	li
		display:inline-block;
		width:pxtorem(750/6px);
		height:pxtorem(80px);
		line-height:pxtorem(80px);
		text-align:center;
		font-size:pxtorem(30px);
		color:#ccc
.moveTab
	position:absolute;
	display:block;
	width:pxtorem(750/6px);
	height:5px;
	background-color:orange;
	bottom:0;
	left:0;
	transition:0.3s;
.homeContent_wrap
	width:pxtorem(750px);
	overflow:hidden;
	.homeContent_inner
		position:relative;
		white-space:nowrap;
		transition:0.3s;
		div
			width:pxtorem(750px);
			height:pxtorem(1000px);
			display:inline-block;
</style>



