const setter = "setter";
const setShopList = "setShopList";
const setShopCarList = "setShopCarList";
const addShopCarList = "addShopCarList";
const setShow="setShow";
const addPayList="addPayList";
const addPayArrList="addPayArrList"
const setBigcomment="setBigcomment"
export default {
	// "SUB_ITEM"
	// 
	[setter] (state,json) {
		state.goods.name = json.name;
		state.goods.index = json.index;
	},
	[setShopList] (state,json) {
		state[json.name].price = json.price,
		state[json.name].size[index].num = json.num
	},
	[addShopCarList] (state,json) {
		state.shopCarList.push(json);
	},
	[setShopCarList] (state,json) {
		// state.shopCarList[json.index].id=json.id,
		state.shopCarList[json.index].bol=json.bol,
		// state.shopCarList[json.index].price=json.price,
		// state.shopCarList[json.index].title=json.title,
		state.shopCarList[json.index].num=json.num
		// state.shopCarList[json.index].url=json.url,
		state.shopCarList[json.index].maxNum=json.maxNum

	},
	[addPayList] (state,json){
		state.payList=[];
		state.payList.push(json);
	},
	[addPayArrList] (state,arr){
		state.payList=[];
		state.payList=arr;
	},
	[setBigcomment](state,data){
		state.Bigcomment=data
	},
};


