<template>
<div class="homeWest">
  <back></back>
	<img src="../../assets/img/home/otherBanner.png" alt="">
	<p></p>
	<ul>
		<li v-for="v in goods">
			<img :src="v.url" alt="">
			<p>{{v.title}}</p>
			<p>{{v.price}}</p>
		</li>
	</ul>
 </div>
</template>

<script>
import back from '../common/back'
import axios from 'axios'
export default {
  data () {
    return {
    	goods:[]
    }
  },
  components:{
  	back:back
  },
  mounted:function(){
  	var that=this;
  	axios.get('/home').then(function(res){
  		that.goods=res.data.westGoods;
  	})
  }
}
</script>


<style scoped lang='stylus'>
@import '../../assets/common/stylus/common.stylus'
.homeWest
	>p
		width: 100%;
		height: pxtorem(300px);
		box-sizing: border-box;
		border: 1px solid black;
	>ul
		width:pxtorem(670px);
		margin:auto;
		margin-top:pxtorem(20px);
		li
			width:pxtorem(320px);
			text-align:center;
			display:inline-block;
		img
			width:pxtorem(320px);
			height:pxtorem(300px);
		li:nth-child(2n)
			margin-left:pxtorem(20px);
			
				
</style>



