<template>
 <div class="commContentT clear">
 	<!-- <ul>
 		<li v-for="(v,k) in newArr">
 			<img :src="v.banner[0]" alt="">
 			<p>{{v.txt}}</p>
 			<div>
 				<img :src="v.authorUrl" alt="">
 				<span>{{v.name}}</span>
 				<img src="../../assets/img/community/good.png" alt="">
 				<span>{{show[k]['head'].length}}</span>
 			</div>
 		</li>
 	</ul>
 	<ul>
 		<li v-for="(v,k) in newArr">
 			<img :src="v.banner[0]" alt="">
 			<p>{{v.txt}}</p>
 			<div>
 				<img :src="v.authorUrl" alt="">
 				<span>{{v.name}}</span>
 				<img src="../../assets/img/community/good.png" alt="">
 				<span>{{show[k]['head'].length}}</span>
 			</div>
 		</li>
 	</ul>
 	 -->
 	 <ul
	  v-infinite-scroll="loadMore"
	  infinite-scroll-disabled="false"
	  infinite-scroll-distance="10">
	  <li v-for="(v,k) in list" v-if="k%2==0">
	  	<img :src="v.banner[0]" alt="">
 			<p>{{v.txt}}</p>
 			<div>
			<img :src="v.authorUrl" alt="">
			<span>{{v.name}}</span>
			<img src="../../assets/img/community/good.png" alt="">
			<span>{{likeFn(k)}}</span>
 		</div>
	  </li>
	</ul>
	<ul
	  v-infinite-scroll="loadMore"
	  infinite-scroll-disabled="true"
	  infinite-scroll-distance="10">
	  <li v-for="(v,k) in list" v-if="k%2!=0">
	  	<img :src="v.banner[0]" alt="">
 			<p>{{v.txt}}</p>
 			<div>
			<img :src="v.authorUrl" alt="">
			<span>{{v.name}}</span>
			<img src="../../assets/img/community/good.png" alt="">
			<span>{{likeFn(k)}}</span>
 		</div>
	  </li>
	</ul>
 </div>
</template>

<script>
import back from '../common/back';
import axios from 'axios';
import { mapGetters, mapActions } from 'vuex';
import { Indicator } from 'mint-ui';
export default {
  data () {
    return {
    	newArr:[],
    	list:[]
    }
  },
  computed:{
  	...mapGetters({
	  	show:"getShow"
 	 })
  },
  methods:{
  	loadMore:function() {
	  this.loading = true;
	  Indicator.open({
		  text: '...',
		  spinnerType: 'fading-circle'
	});
	  setTimeout(() => {
	    for (let i = 1; i <= 5; i++) {
	      this.list.push(this.newArr[i]);
	    }
	    this.loading = false;
	    Indicator.close();
	  }, 1500);
	},
	likeFn:function(k){
		var len;
		if(k>this.show.length-1){
			len=0
		}else{
			len=this.show[k]['head'].length
		}
		
		return len;
	}
  },
  mounted:function(){
  	var that=this;
  	axios.get("/community/recommend",{
  		params: {
          name: "recommend"
        }
  	}).then(function(res){
  		that.list=that.newArr=res.data.show
  	})
  }
 
}
</script>


<style scoped lang='stylus'>

@import '../../assets/common/stylus/common.stylus'
.commContentT
	>ul
		float:left;
		width:pxtorem(350px);
		margin-left:pxtorem(16px);
		li
			padding-top:pxtorem(20px);
			>img
				width:pxtorem(350px);
			p
				padding-top:pxtorem(20px);
				word-wrap:break-word;
			>div
				padding-top:pxtorem(20px);
				img
					vertical-align:middle;
				span
					vertical-align:middle;
				img:first-of-type
					width:pxtorem(55px);
					height:pxtorem(55px);
					border-radius:50%;
				img:nth-of-type(2)
					width:pxtorem(40px);
					height:pxtorem(40px);
					margin-left:pxtorem(40px);
</style>



