<template>
<div class="nav">
 	<ul>
 		<router-link tag="li" :to="v.path" v-for="(v,k) in grayArr">
 			<img :src="v.url" ref="navTab">
 			<p>{{v.title}}</p>
 		</router-link>
 	</ul>
</div>
</template>

<script>
export default {
  data () {
    return {
    	grayArr:[
    	{
    		url:require("../../assets/img/nav/home.png"),
    		title:"",
    		path:"/home"
    	},
    	{
    		url:require("../../assets/img/nav/community.png"),
    		title:"",
    		path:"/community"
    	},
    	{
    		url:require("../../assets/img/nav/cart.png"),
    		title:"",
    		path:"/shopCar"
    	},
    	{
    		url:require("../../assets/img/nav/my.png"),
    		title:"",
    		path:"my"
    	},
    	],
    	lightArr:[require("../../assets/img/nav/home-s.png"),
    	require("../../assets/img/nav/community-s.png"),
    	require("../../assets/img/nav/cart-s.png"),
    	require("../../assets/img/nav/my-s.png")
    	]
    }
  },
  mounted:function(){
  	for(var i=0;i<this.grayArr.length;i++){
  		if(window.location.hash.indexOf(this.grayArr[i].path)!=-1){
  			this.$refs.navTab[i].src=this.lightArr[i];
  			break;
  		}
  		
  	}
  
  }
}
</script>


<style scoped lang='stylus'>
@import '../../assets/common/stylus/common.stylus'
.nav
	position:fixed;
	bottom:0;
	left:0;z-index:100
	ul
		display:flex;
		width:pxtorem(750px);
		height:pxtorem(100px);
		background-color:#f9f9f9;
		border-top:1px solid #ccc;
		li
			flex:1;
			height:pxtorem(100px);
			text-align:center
			img
				margin-top:pxtorem(10px);
				width:pxtorem(45px);
				height:pxtorem(45px);
			p
				font-size:pxtorem(28px);
				color:gray;
				font-weight:bolder;
</style>



