<template>
<div class="wrap" ref="bg">
 <ul class="homeAnimation" @touchstart="move" ref="move">
 	<li>
 		<img src="../../assets/img/home/one_bg.png" alt="">
 	</li>
 	<li>
		<img src="../../assets/img/home/two_bg.png" alt="">
 	</li>
 	<li>
 		<img src="../../assets/img/home/three_bg.png" alt="">
 	</li>
 	<li>
 		<img src="../../assets/img/home/four_bg.png" alt="">
 	</li>
 	<li class="moveAd" ref="moveAd"><img src="../../assets/img/home/box.png" alt=""></li>
 	<li ref="moveOne" class="moveOne"></li>
 	<li ref="moveTwo" class="moveTwo"></li>
 	<li ref="moveThree" class="moveThree"></li>
 	<li ref="moveFour" class="moveFour"></li>
 </ul>
</div>
</template>

<script>

export default {
  data () {
    return {
    	initMoveAd:{}
    }
  },
  methods:{
  	move:function(){

  		//
  		var that=this;
  		var previousX=event.changedTouches[0].clientX;
  		var oL=this.$refs.move.style.left;
  		oL=oL.toString();
  		if(!oL){oL=0}
  		oL=parseInt(oL.toString())


  
  	
  		var initMove=[];

  		initMove.push({})
  		initMove[0].width=this.$refs.moveTwo.offsetWidth;
  		initMove[0].height=this.$refs.moveTwo.offsetHeight;
  		initMove[0].left=this.$refs.moveTwo.offsetLeft;
  		initMove[0].top=this.$refs.moveTwo.offsetTop;

  		initMove.push({})
  		initMove[1].width=this.$refs.moveThree.offsetWidth;
  		initMove[1].height=this.$refs.moveThree.offsetHeight;
  		initMove[1].left=this.$refs.moveThree.offsetLeft;
  		initMove[1].top=this.$refs.moveThree.offsetTop;

  		initMove.push({})
  		initMove[2].width=this.$refs.moveFour.offsetWidth;
  		initMove[2].height=this.$refs.moveFour.offsetHeight;
  		initMove[2].left=this.$refs.moveFour.offsetLeft;
  		initMove[2].top=this.$refs.moveFour.offsetTop;

  		this.$refs.bg.addEventListener("touchmove", function(e){

  			//
  			var mX=oL+event.changedTouches[0].clientX-previousX;
  			if(mX>0){mX=0}
  			that.$refs.move.style.left=mX+"px";


  			//
  			that.initMoveAd.left=that.$refs.moveAd.offsetLeft;
  			that.initMoveAd.top=that.$refs.moveAd.offsetTop;
 			
  			for(var i=0;i<initMove.length;i++){
  				if(that.initMoveAd.left<initMove[i].left){

  					var percent=-mX/(750*(i+1));

					  var iL=initMove[i].left*percent;
  					var iT=initMove[i].top*percent;
  					var mW=initMove[i].width*percent;
  					var mH=initMove[i].height*percent;


  					if(i==0){
  						if(iL<that.$refs.moveOne.offsetLeft){
  							iL=that.$refs.moveOne.offsetLeft;
  							iT=that.$refs.moveOne.offsetTop;
  							mW=that.$refs.moveOne.offsetHeight;
  							mH=that.$refs.moveOne.offsetWidth;
  						}
  					}else{
  						if(iL<initMove[i-1].left){
  							iL=initMove[i-1].left;
  							iT=initMove[i-1].top;
  							mW=initMove[i-1].width;
  							mH=initMove[i-1].height;
  						}
  					}


  					that.$refs.moveAd.style.left=iL+"px";
  					that.$refs.moveAd.style.top=iT+"px";
  					that.$refs.moveAd.style.width=mW+"px";
  					that.$refs.moveAd.style.height=mH+"px";

  					if(i==initMove.length-1&&percent>=0.95){
  						// this.$router.go('/home');

              that.$refs.bg.removeEventListener("touchmove", function(e){})
  						  // window.location.hash="#/home";
                that.$router.push('/home');
  					}
  					break;
  				}
  			}
  			e.stopPropagation();
  		})

  	}

  },
  mounted:function(){
  	this.initMoveAd={
  		width:this.$refs.moveAd.offsetWidth,
  		height:this.$refs.moveAd.offsetHeight,
    	initLeft:this.$refs.moveAd.offsetLeft,
    	initTop:this.$refs.moveAd.offsetTop
    }
  }
}
</script>


<style scoped lang='stylus'>
@import '../../assets/common/stylus/common.stylus'
.wrap
	width:pxtorem(750px);
	height:pxtorem(1334px);	
	overflow:hidden;
	position:absolute;
	div
		position:absolute;
.homeAnimation
	width:pxtorem(750px)*3;
	height:pxtorem(1334px);
	position:relative;
	li
		width:pxtorem(750px);
		height:pxtorem(1334px);
		position:absolute;
		top:0;
		left:0;
		img 
			display:block;
			width:100%;
			height:100%;
	li:nth-child(2)
		left:pxtorem(750px);
	li:nth-child(3)
		left:pxtorem(750px)*2;
	li:nth-child(4)
		left:pxtorem(750px)*3;
.homeAnimation	
	.moveAd
		top:pxtorem(594px);
		left:pxtorem(164px);
		background:pink;
		width:pxtorem(420px);
		height:pxtorem(380px);
		z-index:10;
	.moveOne
		width:pxtorem(420px);
		height:pxtorem(380px);
		top:pxtorem(594px);
		left:pxtorem(164px);
		background:white;
	.moveTwo
		width:pxtorem(450px);
		height:pxtorem(400px);
		top:pxtorem(300px);
		left:pxtorem(900px);
		background:white;
	.moveThree
		width:pxtorem(210px);
		height:pxtorem(210px);
		top:pxtorem(296px);
		left:pxtorem(1880px);
		background:white;
	.moveFour
		width:pxtorem(450px);
		height:pxtorem(410px);
		top:pxtorem(304px);
		left:pxtorem(2400px);
		background:white;
</style>



