<template>
<div class="one">
	<div class="toWest clear">
		<p></p>
		<div>
			<img src="../../assets/img/home/heart.png" alt="" @click="toWest">
		</div>
		<div>
			<img src="../../assets/img/home/menu.png" alt="" @click="toWest">
			<img src="../../assets/img/home/summer.png" alt="" @click="toWest">
		</div>
	</div>
	<div class="newContent">
		<p>-</p>
		<ul @touchmove="stop">
			<li v-for="(v,k) in newGoods" @click="goodsInfo({'name':'newShop','index':k})">
				<img :src="v.url" alt="">
				<p>{{v.title}}</p>
				<p>{{v.price}}</p>
			</li>
		</ul>
		<div> ></div>
	</div>
	<div class="kill">
		<p> - </p>
		<div class="timeout">
			<img src="../../assets/img/home/timeOut.png" alt="">
			<span>113</span>
			<span></span>
			<span>0:00-0:30</span>
		</div>
		<mt-swipe :auto="0" class="killSwiper" :show-indicators="false" :continuous="false" @change="handleChange" :stopPropagation="true">
		  <mt-swipe-item class="clear" v-for="(v,k) in killGoods">
		  	<div class="clear killGoods" @click="goodsInfo({'name':'killShop','index':v.id1})">
				<img :src="v.url1" alt="">
				<div>
					<p>{{v.title1}}</p>
					<p><span style="color:red">{{v.price1}}</span><del>{{v.prePrice1}}</del></p>
					<button>
						<img src="../../assets/img/home/shopCar.png" alt="">
						<span></span>
					</button>
				</div>
			</div>
			<div class="clear killGoods" @click="goodsInfo({'name':'killShop','index':v.id2})">
				<img :src="v.url2" alt="">
				<div>
					<p>{{v.title2}}</p>
					<p><span style="color:red">{{v.price2}}</span><del>{{v.prePrice2}}</del></p>
					<button>
						<img src="../../assets/img/home/shopCar.png" alt="">
						<span></span>
					</button>
				</div>
			</div>
		  </mt-swipe-item>
		</mt-swipe>
		<mt-progress :value="mtProgressV" :bar-height="5"></mt-progress>
	</div>
</div>
</template>

<script>
import axios from 'axios';
import {mapActions} from 'vuex';
export default {
  data () {
    return {
    	newGoods:[],
    	killGoods:[],
    	mtProgressV:0
    }
  },
  copmonents:{

  },
  methods:{
	handleChange(index){
		this.mtProgressV=index/(this.killGoods.length-1)*100;
	},
	stop(e){
		e.stopPropagation();
	},
	toWest:function(){
		this.$router.push('/west');
	},
	...mapActions([
  		'setItem'
  	]),
	goodsInfo:function(json){
		this.setItem(json);
		// console.log(this.$store.getters)
		this.$router.push('/shop')
	},
  },
  mounted:function(){
  	var that=this;
  	 axios.get("/home").then(function(res){
        that.newGoods=res.data.newGoods;
        var arr=[];
        var data=res.data.killGoods;
        var index=0;
        for(var i=0;i<data.length/2;i++){
        	arr[i]={};
        	arr[i].title1=data[index].title;
        	arr[i].price1=data[index].price;
        	arr[i].prePrice1=data[index].prePrice;
        	arr[i].url1=data[index].url;
        	arr[i].id1=index;
        	index++;
        	arr[i].title2=data[index].title;
        	arr[i].price2=data[index].price;
        	arr[i].url2=data[index].url;
        	arr[i].prePrice2=data[index].prePrice;
        	arr[i].id2=index;
        	index++;
        }
        that.killGoods=arr;
      });
  }

}
</script>


<style scoped lang='stylus'>
@import '../../assets/common/stylus/common.stylus'
::-webkit-scrollbar
	width: 0;
	height: 0;
	display: none;
.one
	>div:last-child
		padding-bottom:pxtorem(100px);
.toWest
	p
		text-align:center;
		font-size:pxtorem(36px);
		line-height:2;
	div
		float:left
		width:pxtorem(340px);
		height:pxtorem(340px);
		margin:pxtorem(10px) 0 0 pxtorem(20px);
	div:first-child
		img
			width:100%;
			height:100%;
	div:last-child
		white-space:normal;
		img
			width:100%;
			height:50%;
.newContent
	margin-top:pxtorem(20px);
	
	>p
		text-align:center;
		font-size:pxtorem(36px);
	ul
		overflow:scroll;
		overflow-y:hidden;
		width:pxtorem(750px);
		white-space:nowrap;
		margin-top:pxtorem(20px);
		li
			width:pxtorem(270px);
			height:pxtorem(410px);
			display:inline-block;
			text-align:center;
			margin-left:pxtorem(10px);
			img
				width:pxtorem(270px);
				height:pxtorem(270px);
			p:last-child
				color:red;
	>div
		text-align:center;
		font-size:pxtorem(30px);
.kill
	background:linear-gradient(#fafafa, #ffffff);
	margin-top:pxtorem(20px);
	p
		text-align:center;
		font-size:pxtorem(32px);
		padding-top:pxtorem(20px);
	.timeout
		text-align:center;
		margin:pxtorem(20px) 0;
		img
			width:pxtorem(32px);
			height:pxtorem(25px);
			vertical-align:middle;
	ul
		width:pxtorem(750px);
		white-space:no-wrap;
		overflow-x:scroll;
		overflow-y:hidden;
		li
			width:pxtorem(750px);
			display:inline-block;
			div
				>img
					width:pxtorem(330px);
					height:pxtorem(330px);
					float:left;
				div
					float:left;
					width:pxtorem(330px);
					height:pxtorem(330px);
					text-align:center;
					p:first-child
						padding:pxtorem(40px) 0;
					p:nth-child(2)
						padding:pxtorem(40px) 0;
					button
						img,span
							vertical-align:middle;
						img
							width:pxtorem(30px);
							height:pxtorem(24px);			
.killSwiper
	width:pxtorem(750px);
	height:pxtorem(700px);
	.killGoods
		>img
			width:pxtorem(330px);
			height:pxtorem(330px);
			float:left;
			margin-top:pxtorem(20px);
		div
			float:left;
			width:pxtorem(330px);
			height:pxtorem(330px);
			text-align:center;
			p:first-child
				padding:pxtorem(40px) 0;
			p:nth-child(2)
				padding:pxtorem(40px) 0;
			button
				border:none;
				outline:none;
				width:pxtorem(150px);
				height:pxtorem(70px);
				font-size:pxtorem(20px);
				font-weight:bolder;
				img,span
					vertical-align:middle;
				img
					width:pxtorem(30px);
					height:pxtorem(24px);
</style>



