<template>
	<div class="community">
 		<tL-header search=false>
	 		<span slot="left">+</span>
	 		<img slot="right" src="../../assets/img/header/message.png">
	 	</tL-header>
	 	<tL-nav></tL-nav>
	 	<tL-commContent></tL-commContent>
	</div>
</template>

<script>
import header from '../common/header';
import nav from '../common/nav';
import commContent from './commContent';

export default {
  data () {
    return {
    }
  },
  components:{
  	"tL-header":header,
  	"tL-nav":nav,
  	"tL-commContent":commContent
  }
}
</script>


<style scoped lang='stylus'>
@import '../../assets/common/stylus/common.stylus'

</style>



