<template>
 <div class="killShop">
 <back></back>
 	<mt-swipe :auto="0" class="banner">
	  <mt-swipe-item v-for="v in killShop.banner">
	  	<img :src="v" alt="">
	  </mt-swipe-item>
	</mt-swipe>
	<div class="info">
		<h2>{{killShop.title}}</h2>
		<p><span>{{minTomaxPrice()}}</span></p>
		<ul class="protect">
			<li v-for="v in killShop.protect">
				<img src="../../assets/img/shop/shop/gou.png" alt="">
				<span>{{v}}</span>
			</li>
		</ul>
	</div>
	<div class="size" @click="selectBol=!selectBol">
		<span></span>
		<img src="../../assets/img/shop/shop/icon-plus.png" alt="">
	</div>
	<p class="reason"></p>
	<p class="reason">{{killShop.txt}}</p>
	<div class="picTxt" @touchstart="look"></div>
	<ul class="detail" v-if="detailBol">
		<li v-for="v in killShop.detail">
			<img :src="v" alt="">
		</li>
	</ul>
	<!-- <shopNav></shopNav> -->
	<div class="selectSize" v-show="selectBol" @click="selectBol=!selectBol">
		<div @click="stop">
			<div class="selectO">
				<div class="selectImg" >
					<mt-tab-container v-model="sizeArrIndex">
					  <mt-tab-container-item :id="k" v-for="(v,k) in killShop.select">
					    <img :src="v" alt="">
					  </mt-tab-container-item>
					</mt-tab-container>
				</div>
				<div class="selectNum">
					<ul>
						<li><span class="price"> {{sizeArr.size[sizeArrIndex]["price"]}}</span><span>{{sizeArr.size[sizeArrIndex]["num"]-buyNum>0?sizeArr.size[sizeArrIndex]["num"]-buyNum:0}}</span></li>
						<li>{{sizeArr.size[sizeArrIndex]["kind"]}}</li>
						<li class="num clear">
							<span @click="reduce">-</span>
							<input type="number" v-model="buyNum" @input="reduceAdd">
							<!-- <span>{{buyNum}}</span> -->
							<span @click="add">+</span>
						</li>
					</ul>
				</div>
			</div>
			<div class="selectT">
				<p></p>
				<ul>
					<li v-for="(v,k) in sizeArr.size" @click="selectSize(k)" :style="sizeArrIndex==k?'background:red':''">{{v.kind}}</li>
				</ul>
			</div>
			<div class="buyBtn"><button @click="joinCar"></button><button @click="buyNow"></button></div>
		</div>
	</div>
	<div class="shopNav">
	 	<div class="shopNav_car">
	 		<img src="../../assets/img/shop/shop/shopCarOn.png" alt="">
	 		<div>{{num}}</div>
	 	</div>
		<div  @click="joinCar"></div>
		<div @click="buyNow"></div>
	 </div>
 </div>
</template>

<script>
import back from '../common/back';
import { mapGetters, mapActions } from 'vuex';
import axios from 'axios';
// import shopNav from './shopNav';
export default {
  data () {
    return {
    	killShop:{},
    	detailBol:false,
    	sizeArr:{},
    	sizeArrIndex:0,
    	buyNum:1,
    	selectBol:false
    }
  },
  components:{
  	back:back,
  	// shopNav:shopNav
  },
  methods:{
  	...mapActions([
  		"addShopCar",
  		"setShopCar",
  		"addPay"
  	]),
  	buyNow:function(){
  		
  		var json={};
  		for(var v in this.sizeArr.size[this.sizeArrIndex]){
  			json[v]=this.sizeArr.size[this.sizeArrIndex][v];
  		}
  		json.url=this.killShop.select[this.sizeArrIndex];
  		json.num=this.buyNum;
  		this.addPay(json);
  		this.$router.push("/pay");
  	},
  	look:function(){
  		var bol=this.detailBol;
  		if(this.detailBol){
  			event.currentTarget.innerText="";
  		}else{
  			event.currentTarget.innerText="";
  		} 
  		bol=!bol;
  		this.detailBol=bol;
  	},
  	selectSize:function(index){
  		this.sizeArrIndex=index;
  		if(this.sizeArr.size[this.sizeArrIndex]["num"]==0){
  			this.buyNum=0;return;
  		}
  		this.buyNum=1;
  	},
  	add:function(){
  		var num=this.buyNum;
  		num++;
  		if(num>=this.sizeArr.size[this.sizeArrIndex]["num"]){
  			num=this.sizeArr.size[this.sizeArrIndex]["num"]
  		}
  		this.buyNum=num;
  	},
  	reduce:function(){
  		if(this.sizeArr.size[this.sizeArrIndex].num==0){
  			console.log("aaa")
  			this.buyNum=0;return;
  		}
  		var num=this.buyNum;
  		num--;
  		if(num<=1){
  			num=1
  		}
  		this.buyNum=num;

  	},
  	reduceAdd:function(){
  		console.log("aaa")
  		var num=this.buyNum;
  		if(num>=this.sizeArr.size[this.sizeArrIndex]["num"]){
  			num=this.sizeArr.size[this.sizeArrIndex]["num"]
  		}else if(num<=1){
  			num=1
  		}
  		console.log(num)
  		this.buyNum=num;
  	},
  	stop:function(e){
  		e.stopPropagation();
  	},
  	minTomaxPrice:function(){
  		var max=this.sizeArr.size[0]["price"];
  		var min=this.sizeArr.size[0]["price"];
  		for(var i=1;i<this.sizeArr.size.length;i++){
  			if(this.sizeArr.size[i]["price"]>max){
  				max=this.sizeArr.size[i]["price"];
  			}
  			if(this.sizeArr.size[i]["price"]<min){
  				min=this.sizeArr.size[i]["price"];
  			}
  		}
  		return " "+min+"~~"+" "+max;
  	},
  	joinCar:function(){
  		var arr=this.sizeArr.size[this.sizeArrIndex]
  		var num=arr.num
  		if(num<0){
  			return;
  		}
  		var id=arr.id;
  		var priceC=arr.price;
  		var kindC=arr.kind;
  		this.addShopCar({bol:true,price:priceC,kind:kindC,num:this.buyNum,url:this.killShop.select[this.sizeArrIndex],title:this.killShop.title,id:arr.id,maxNum:num});
  		if(this.sizeArr.size[this.sizeArrIndex]["num"]-this.buyNum<=0){
  			this.buyNum=0;
  			this.sizeArr.size[this.sizeArrIndex]["num"]=0
  		}else{
  			this.sizeArr.size[this.sizeArrIndex]["num"]=this.sizeArr.size[this.sizeArrIndex]["num"]-this.buyNum;
  			this.buyNum=1;
  		}
  		
  		this.selectBol=false;
  		

  	}
  },
  mounted:function(){
  	var that =this;
  	axios.get('/home').then(function(res){
  		that.killShop=res.data[that.$store.getters.getGoods.name][Number(that.$store.getters.getGoods.index)];
  	})
  	this.sizeArr=this.$store.getters.getShop[that.$store.getters.getGoods.name][that.$store.getters.getGoods.index];
  	if(this.sizeArr.size[this.sizeArrIndex]["num"]==0){
  		this.buyNum=0;
  	}
 
  },
  computed: mapGetters({
  	num : 'getShopCarListLen'
  }),
}
</script>


<style scoped lang='stylus'>

@import '../../assets/common/stylus/common.stylus'
.banner
	width:pxtorem(750px);
	height:pxtorem(600px);
	img
		width:100%;
		height:100%;
.info
	padding-left:pxtorem(30px);
	h2
		padding:pxtorem(20px) 0;
	>p
		padding:pxtorem(20px) 0;
		color:red;
		font-size:pxtorem(30px);
.protect
	padding:pxtorem(20px) 0;
	li
		display:inline-block;
		width:pxtorem(206px);
		height:pxtorem(30px);
		font-size:pxtorem(24px);
		margin-top:pxtorem(10px);
		img
			width:pxtorem(36px);
			height:pxtorem(36px);
		img,span
			vertical-align:middle;
.size
	padding-left:pxtorem(30px);
	width:pxtorem(750px);
	height:pxtorem(100px);
	line-height:pxtorem(100px);
	border-top:1px solid #ccc;
	border-bottom:1px solid #ccc;
.reason
	padding-left:pxtorem(20px);
	padding-top:pxtorem(10px);
.picTxt
	margin-top:pxtorem(40px);
	padding:pxtorem(30px) 0;
	text-align:center;
	border-top:1px solid #ccc;
	border-bottom:1px solid #ccc;
.detail
	li
		width:pxtorem(750px);
		img
			width:100%;
.selectSize
	width:pxtorem(750px);
	height:pxtorem(1334px);
	background:rgba(0,0,0,0.7);
	position:fixed;
	top:0;
	left:0;
	z-index:1001;
	>div
		background:white;
		width:pxtorem(750px);
		height:pxtorem(850px);
		position:absolute;
		bottom:pxtorem(122px);
.selectO
	width:pxtorem(710px);
	margin:auto;
	padding-top:pxtorem(20px);
.selectImg
	width:pxtorem(160px);
	height:pxtorem(160px);
	display:inline-block;
	vertical-align:top;
	img
		width:pxtorem(160px);
		height:pxtorem(160px);
.selectNum
	display:inline-block;
	vertical-align:top;
	margin-left:pxtorem(10px);
	li:nth-child(2)
		margin-top:pxtorem(20px);
.price
	color:red;
.num
	margin-top:pxtorem(20px);
	span
		border:1px solid black;
		float:left;
		width:pxtorem(60px);
		height:pxtorem(60px);
		text-align:center;
		line-height:pxtorem(60px);
	input
		float:left;
		width:pxtorem(60px);
		height:pxtorem(60px);
		text-align:center;
		border:1px solid black;
.selectT
	p
		margin-top:pxtorem(20px);
	li
		display:inline-block;
		width:pxtorem(200px);
		height:pxtorem(65px);
		line-height:pxtorem(65px);
		text-align:center;
		margin-left:pxtorem(20px);
		margin-top:pxtorem(10px);
		border:1px solid #ccc;
.buyBtn
	position:absolute;
	bottom:0;
	left:0;
	button
		width:pxtorem(375px);
		height:pxtorem(80px);
		border:none;
		outline:none;
		font-size:pxtorem(30px)
	button:first-child
		background:orange;
	button:last-child
		background:red;
.shopNav
	position:fixed;
	bottom:0;
	left:0;
	width:pxtorem(750px);
	height:pxtorem(100px);
	padding-top:pxtorem(20px);
	border-top:1px solid gray;
	z-index:1002;
	background:white;
	>div:nth-child(2),div:nth-child(3)
		width:pxtorem(200px);
		height:pxtorem(65px);
		display:inline-block;
		border-radius:pxtorem(10px);
		text-align:center;
		line-height:pxtorem(65px);
		position:absolute;
		border:1px solid gray;
	>div:nth-child(2)
		right:pxtorem(240px);
	>div:nth-child(3)
		background:yellow;
		right:pxtorem(20px);
.shopNav_car
	width:pxtorem(100px);
	height:pxtorem(80px);
	line-height:pxtorem(80px);
	display:inline-block;
	position:absolute;
	text-align:center;
	left:pxtorem(10px);
	img
		width:pxtorem(65px);
		height:pxtorem(65px);
		vertical-align:middle;
	div
		width:pxtorem(36px);
		height:pxtorem(36px);
		background:red;
		position:absolute;
		top:0;
		right:0;
		border-radius:50%;
		color:purple;
		line-height:pxtorem(36px);
		text-align:center;
</style>



