<template>
 <div class="sCContent">
 	<ul class="goodsList"  v-for="(v,k) in list" :class="k==num-1?'bottom':''">
 		<li><img :src="v.bol?gouArr[0]:gouArr[1]" alt="" @click="light(k)"></li>
 		<li><img :src="v.url" alt=""></li>
 		<li>
 			<p>{{v.title}}</p>
 			<p style="color:red">{{v.price}} {{v.price*v.num}}</p>
 			<p>{{v.kind}}</p>
 		</li>
 		<li>
 			<div>
	 			<!-- <span @click="reduce(k)">
	 			-
	 			</span><span>
	 			{{v.num}}
	 			</span><span @click="add(k)">
	 			+
	 			</span> -->
	 			<span @click="reduce(k)">
	 			-
	 			</span><input type="number" v-model="v.num" @input="reduceAdd(k)"></span><span @click="add(k)">
	 			+
	 			</span>
 			</div>
 		</li>
 	</ul>
 	 <div v-if="!num" class="noneOfCar"></div>
 	<ul class="buy">
 		<li><img :src="AllSelectBol?gouArr[0]:gouArr[1]" alt="" @click="allSelect"><span></span></li>
 		<li class="sum">()<span>{{sum}}</span></li>
 		<li v-if="edit" @click="buyNow"></li>
 		<li v-else style="background:red" @click="del"></li>
 	</ul>
 </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import bus from '../../../static/js/bus';
import { Toast } from 'mint-ui';
export default {
  data () {
    return {
    	gouArr:[require('../../assets/img/shop/shop/gou-red.png'),require('../../assets/img/shop/shop/gou-gray.png')],
    	sum:0,
    	AllSelectBol:true,
    	edit:true
    }
  },
  computed:{
  	...mapGetters({
	  	list:"getShopCarList",
	  	num:"getShopCarListLen",
	  	
 	 })
  },
  mounted:function(){
  	// console.log(this.list)
  	var sum=0;
  	for(var i =0;i<this.num;i++){
	  		if(this.list[i].bol){
	  			sum+=this.list[i].num*this.list[i].price;
	  		}else{
	  			this.AllSelectBol=false;
	  			continue;
	  		}
	  		
	  	}
  	this.sum=sum;
  	var that = this;
  	bus.$on('edit',function(data){
  		that.edit=data;
  		for(var i=0;i<that.list.length;i++){
  			that.list[i].bol=data;
  			that.list[i].index=i;
  			that.AllSelectBol=data;
  			that.setShopCar(that.list[i])

  		}
  		that.sumCount();
  	})
  },
  methods:{
  	...mapActions([
  		"setShopCar",
  		'addPay',
  		'addPayArr'
  	]),
  	sumCount:function(){
  		var sum=0;
  		var bol=true;
	  	for(var i =0;i<this.num;i++){
	  		if(this.list[i].bol){
	  			sum+=this.list[i].num*this.list[i].price;
	  		}else{
	  			bol=false;
	  			continue;
	  		}
	  		
	  	}
	  	this.sum=sum;
	  	this.AllSelectBol=bol;
  	},
  	add:function(index){
  		var num=Number(this.list[index].num);
  		num++;
  		if(num>this.list[index].maxNum){
			num=this.list[index].maxNum
		}else if(num<=1||!num){this.list[index].num=1;}
 		this.list[index].num = num;
 		this.list[index].index=index;
 		this.setShopCar(this.list[index]);
 		this.sumCount();
  	},
  	reduce:function(index){
  		var num=Number(this.list[index].num);
  		num--;
  		if(num<=1){num=1};
 		this.list[index].num = num;
 		this.list[index].index=index;
 		console.log(this.list[index])
 		this.setShopCar(this.list[index])
 		this.sumCount();
  	},
	reduceAdd:function(index){
		var num=Number(this.list[index].num);
		if(num>this.list[index].maxNum){
			this.list[index].num=this.list[index].maxNum
		}else if(num<=1||!num){this.list[index].num=1;}
		this.setShopCar(this.list[index]);
		this.sumCount();
  	},
  	light:function(index){
  		var bol=this.list[index].bol;
  		bol=!bol;
  		this.list[index].bol=bol;
  		this.list[index].index=index;
  		this.setShopCar(this.list[index]);
  		this.sumCount();
  // 		for(var i=0;i<this.list.length;i++){
  // 			if(this.list[index].bol==false){
  // 				return;
  // 			}else{
  // 				bol=true;
  // 			}
  // 		}
		// this.AllSelectBol=bol;
  	},
  	allSelect:function(){
  		console.log("aaa")
  		var bol=this.AllSelectBol;
  		bol=!bol;
  		this.AllSelectBol=bol;
  		for(var i=0;i<this.num;i++){
  			this.list[i].bol=bol;
  		}
  		this.sumCount();
  	},
  	del:function(){
  		var arr=[];
  		for(var i=0;i<this.list.length;i++){
  			if(this.list[i].bol==true){
  				arr.push(i)
  			}
  		}
  		if(arr.length==0){
  			Toast({
			  message: '',
			  position: 'middle',
			  className: "toast"
			})
			return;
  		}
  		arr=arr.reverse();
  		for(var i=0;i<arr.length;i++){
  			console.log(arr[i])
  			this.list.splice(arr[i],1);
  		}
  		this.sumCount();
  	},
  	buyNow:function(){
  		this.addPayArr(this.list)
  		this.$router.push("/pay");
  	}
  }
}
</script>


<style scoped lang='stylus'>
@import '../../assets/common/stylus/common.stylus'
.bottom
	padding-bottom:pxtorem(175px);
.sCContent
	padding-top:pxtorem(182px);
.buy
	width:pxtorem(750px);
	height:pxtorem(75px);
	line-height:pxtorem(75px);
	position:fixed;
	display:flex;
	bottom:pxtorem(102px);
	text-align:center;
	background:white;
	li:first-child
		flex:2;
		img
			width:pxtorem(36px);
			height:pxtorem(36px);
			vertical-align:middle;
		span
			vertical-align:middle;
			font-size:pxtorem(25px);
			margin-left:pxtorem(20px);
	li:nth-child(2)
		flex:4;
	li:nth-child(3)
		flex:3;
		background:yellow;
.sum
	span
		color:red;
.goodsList
	width:pxtorem(750px);
	display:flex;
	// background:purple;
	height:pxtorem(150px);
	line-height:pxtorem(150px);
	padding-top:pxtorem(20px);
	li:first-child
		flex:1;
		text-align:center;
		img
			width:pxtorem(40px);
			height:pxtorem(40px);
			vertical-align:middle;
	li:nth-child(2)
		flex:2;
		img
			width:100%;
			height:100%;
	li:nth-child(3)
		flex:3;
		line-height:normal;
		overflow:hidden;
		p:first-child
			white-space:nowrap;
			overflow:hidden;
			text-overflow:ellipsis;
	li:nth-child(4)
		flex:2;
		position:relative;
		div
			position:absolute;
			height:pxtorem(40px);
			bottom:pxtorem(10px);
			right:pxtorem(20px);
			input
				vertical-align:top;
				width:pxtorem(40px);
				height:pxtorem(40px);
				text-align:center;
				border:1px solid gray;
			span
				vertical-align:top;
				display:inline-block;
				width:pxtorem(40px);
				height:pxtorem(40px);
				line-height:pxtorem(40px);
				text-align:center;
				border:1px solid gray;
.noneOfCar
	text-align:center;
	padding-top:pxtorem(100px);
</style>


