<template>
	<div class="homeContent">
		<div class="homeContent_nav">
			<ul>
				<li v-for="(v,k) in nav" @touchstart="moveTab(k)">{{v}}</li>
			</ul>
			<span class="moveTab" ref="moveTab"></span>
		</div>
		<mt-tab-container v-model="active" :swipeable="true">

		  <mt-tab-container-item id="0">

		   <homeContentO></homeContentO>

		  </mt-tab-container-item>

		  <mt-tab-container-item id="1">

		    <homeContentT></homeContentT>

		  </mt-tab-container-item>

		  <mt-tab-container-item id="2">

		   	<homeContentT></homeContentT>

		  </mt-tab-container-item>

		   <mt-tab-container-item id="3">

		   	<homeContentT></homeContentT>

		  </mt-tab-container-item>

		   <mt-tab-container-item id="4">

		   	<homeContentT></homeContentT>

		  </mt-tab-container-item>

		</mt-tab-container>

	</div>
</template>

<script>
import homeContentO from './homeContentO';
import homeContentT from './homeContentT'
export default {
  data () {
    return {
    	nav:["","","","",""],
    	active:"0"
    }
  },
  components:{
  	homeContentO:homeContentO,
  	homeContentT:homeContentT
  },
  watch:{
  	active:function(){
  		this.$refs.moveTab.style.left=Number(this.active)*2+"rem";
  	}
  },
  methods:{
  	moveTab:function(index){
  		this.$refs.moveTab.style.left=index*2+"rem";
  		// this.$refs.moveContent.style.left=-index*10+"rem";
  		this.active=index.toString();
  	}
  },
  mounted:function(){

  }
}
</script>


<style scoped lang='stylus'>
@import '../../assets/common/stylus/common.stylus'
.homeContent_nav
	position:relative;
	border-bottom:1px solid #ccc;
	padding-top:pxtorem(120px);
ul
	white-space:nowrap
	li
		display:inline-block;
		width:pxtorem(750/5px);
		height:pxtorem(80px);
		line-height:pxtorem(80px);
		text-align:center;
		font-size:pxtorem(30px);
		color:#ccc
.moveTab
	position:absolute;
	display:block;
	width:pxtorem(750/5px);
	height:5px;
	background-color:orange;
	bottom:0;
	left:0;
	transition:0.3s;
// .homeContent_wrap
// 	width:pxtorem(750px);
// 	overflow:hidden;
// 	.homeContent_inner
// 		position:relative;
// 		white-space:nowrap;
// 		transition:0.3s;
// 		div
// 			width:pxtorem(750px);
// 			display:inline-block;
// 			overflow:hidden;
// 			vertical-align:top;
</style>



