import Vue from 'vue'
import Router from 'vue-router'
import start from '../components/home/startPart'
import home from '../components/home/home'
import community from '../components/community/community'
import shopCar from '../components/shopCar/shopCar'
import my from '../components/my/my'
import west from '../components/home/homeWest'
import shop from '../components/shop/shop'
import pay from '../components/pay/pay'
import comment from '../components/community/comment'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: start
    },
    {
      path: '/home',
      component: home
    },
    {
      path: '/community',
      component: community
    },
    {
      path: '/shopCar',
      component: shopCar
    },
    {
      path: '/my',
      component: my
    },
    {
      path:'/west',
      component:west
    },
    {
      path:'/shop',
      component:shop
    },
    {
      path:'/pay',
      component:pay
    },
    {
      path:'/comment',
      component:comment
    }
  ]
})



