export default {
	// 
	setItem({commit, state},json){
		// mutations
		commit('setter',json);
	},
	setShop({commit, state},json){
		// mutations
		commit('setShopList',json);
	},
	setShopCar({commit,state},json){
		commit('setShopCarList',json)
	},
	addShopCar({commit,state},json){
		for(var i=0;i<state.shopCarList.length;i++){
			if(json.id==state.shopCarList[i].id){
				json.index=i;
				json.num=json.num+state.shopCarList[i].num;
				console.log(json);
				commit('setShopCarList',json);
				return;
			}
		}
		commit('addShopCarList',json)
	},
	addPay({commit,state},json){
		commit('addPayList',json)
	},
	addPayArr({commit,state},arr){
		commit('addPayArrList',arr)
	},
	setShow({commit,state},json){
		commit('setShow',json)
	},
	setBigcomment({commit,state},data){
		commit('setBigcomment',data)
	}
};


