export default {
	getGoods(state){
		return state.goods
	},
	getShop(state){
		return {"newShop":state.newShop,"killShop":state.killShop}
	},
	getShopCarList(state){
		return state.shopCarList;
	},
	getShopCarListLen(state){
		return state.shopCarList.length;
	},
	getShow(state){
		return state.show;
	},
	getPay(state){
		return state.payList
	},
	getBigcomment(state){
		return state.Bigcomment
	}
};


